import { Button, Input, message, Modal, Radio, Select, Spin, Table, Tooltip } from 'antd';
import adminApi from '../../../apis/adminApi';
import helpers from '../../../helpers';
import React, { useCallback, useEffect, useState } from 'react';
import OrderDetail from '../../AccountPage/OrderList/OrderDetail/index';
import AdminSearch from '../../../components/AdminSearch';
let isSearch = false;
function generateFilterOrder() {
  let result = [];
  for (let i = 0; i < 7; ++i) {
    result.push({ value: i, text: helpers.convertOrderStatus(i) });
  }
  return result;
}

function OrderList() {
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [orderDetails, setOrderDetails] = useState({
    isOpen: false,
    orderId: '',
  });
  const [page, setPage] = useState(1)
  const [searchOption, setSearchOption] = useState(null)
  const [searchValue, setSearchValue] = useState('')
  const [totalPage, setTotalPage] = useState(0)
  // event: Cập nhật trạng thái đơn hàng
  const updateOrderStatus = async (id, orderStatus) => {
    try {
      const response = await adminApi.postUpdateOrderStatus(id, orderStatus);
      if (response) {
        message.success('Cập nhật thành công');
        setData(
          data.map((item) =>
            item.id === id ? { ...item, orderStatus } : { ...item },
          ),
        );
      }
    } catch (error) {
      message.error('Cập nhật thất bại');
    }
  };

  // modal cập nhật trạng thái đơn hàng
  function UpdateOrderStatusModal(defaultVal = 0, orderId) {
    let valueCurr = defaultVal;
    // console.log("defaultVal: ", defaultVal);
    const modal = Modal.info({
      width: 768,
      title: `Cập nhật trạng thái đơn hàng #${orderId}`,
      content: (
        <Radio.Group
          defaultValue={defaultVal}
          onChange={(v) => (valueCurr = v.target.value)}
          className="m-t-12">
          {generateFilterOrder().map((item, index) => (
            <Radio className="m-b-8" key={index} value={item.text} >
              {item.text}
            </Radio>
          ))}
        </Radio.Group>
      ),
      centered: true,
      icon: null,
      okText: 'Cập nhật',
      onOk: () => {
        updateOrderStatus(orderId, valueCurr);
        modal.destroy();
      },
    });
  }

  const columns = [
    {
      title: 'Mã đơn hàng',
      key: 'orderCode',
      dataIndex: 'orderCode',
      render: (v) => <a>{v}</a>,
    },
    {
      title: 'Mã KH',
      key: 'owner',
      dataIndex: 'owner',
    },
    {
      title: 'Tên',
      key: 'name',
      dataIndex: 'name',
    },
    {
      title: 'Ngày đặt',
      key: 'orderDate',
      dataIndex: 'orderDate',
      sorter: (a, b) => {
        if (a.orderDate > b.orderDate) return 1;
        if (a.orderDate < b.orderDate) return -1;
        return 0;
      },
    },
    // {
    //   title: 'Sản phẩm',
    //   key: 'prodName',
    //   dataIndex: 'prodName',
    //   render: (prodName, record) => (
    //     <Tooltip title={prodName}>
    //       <Link to={`/product/${record.idProduct}`}>
    //         {helpers.reduceProductName(prodName, 30)}
    //       </Link>
    //     </Tooltip>
    //   ),
    // },
    {
      title: 'Tổng tiền',
      key: 'totalMoney',
      dataIndex: 'totalMoney',
      render: (value) => (
        <b style={{ color: '#333' }}>{helpers.formatProductPrice(value)}</b>
      ),
      sorter: (a, b) => a.totalMoney - b.totalMoney,
    },
    {
      title: 'HT thanh toán',
      key: 'paymentMethod',
      dataIndex: 'paymentMethod',
      render: (value) => helpers.convertPaymentMethod(value),
    },
    {
      title: 'Trạng thái đơn hàng',
      key: 'orderStatus',
      dataIndex: 'orderStatus',
      filters: generateFilterOrder(),
      onFilter: (value, record) => record.orderStatus === value,
      // render: (value) => helpers.convertOrderStatus(value),
      // render: (value) => value[0].toUpperCase() + value.slice(1)
      render: (value) => value
    },
    {
      title: 'Hành động',
      render: (_v, records) => (
        <>
          <div>
            <Button
              type="primary"
              className=' w-86px'
              onClick={() =>
                UpdateOrderStatusModal(
                  records.orderStatus,
                  // truyền order status vào dạng text luôn lưu ý format chữ in hoa in thường
                  records.orderCode,
                )
              }>
              Cập nhật
            </Button>
          </div>
          <div className='m-t-10'>
            <Button
              className=' w-86px'
              type="danger"
              onClick={() => setOrderDetails({ isOpen: true, orderId: records.orderCode })}
            >
              Chi tiết
            </Button>
          </div>
        </>
      ),
    },
  ];
  const onSearch = useCallback(async (page, option, value, search) => {
    try {
      let result = await adminApi.getOrderListBy(page, option, value);
      // console.log(result.data.rows);
      let data = result.data.rows.map((item, index) => {
        return {
          key: index,
          owner: item.user_id,
          orderCode: item.id,
          orderDate: helpers.formatOrderDate(item.createdAt),
          totalMoney: item.total,
          paymentMethod: item.payment,
          orderStatus: item.status,
          name: item.user.name
        };
      });
      // console.log(data);
      setData(data);
      setTotalPage(result.data.count)
      setSearchOption(option);
      setSearchValue(value);
      setIsLoading(false);
      if (isSearch) setPage(page)

    } catch (error) {
      console.log(error);
      message.error("Có lỗi xảy ra")
    }

  },
    [],
  )

  useEffect(() => {
    let isSubscribe = true;
    async function getOrderList() {
      try {
        setIsLoading(true);
        const response = await onSearch(page, searchOption, searchValue, false);
        if (isSubscribe && response) {
          const { rows } = response.data;
          const newList = rows.map((item, index) => {
            return {
              key: index,
              owner: item.user_id,
              orderCode: item.id,
              orderDate: helpers.formatOrderDate(item.createdAt),
              totalMoney: item.total,
              paymentMethod: item.payment,
              orderStatus: item.status,
              name: item.user.name
            };
          });
          setData([...newList]);
          setIsLoading(false);
        }
      } catch (error) {
        if (isSubscribe) setIsLoading(false);
      }
    }
    getOrderList();
    return () => {
      isSubscribe = false;
      isSearch = false
    };
  }, [page]);

  return (
    <>
      <div className='m-lr-10 m-t-10'>
        <AdminSearch
          options={[{ text: "Tất Cả", id: 0 }, { text: "Tên khách hàng", id: 1 }, { text: "Mã đơn hàng", id: 2 }]}
          onSearch={onSearch}
        />
        <Table
          columns={columns}
          dataSource={data}
          pagination={{
            showLessItems: true,
            position: ['bottomCenter'],
            total: totalPage,
            onChange: (p) => {
              isSearch = false;
              setPage(p)
            }
          }}
          loading={isLoading}
        />
        {orderDetails.isOpen &&
          <OrderDetail
            orderId={orderDetails.orderId}
            onClose={() => setOrderDetails({ isOpen: false })}
          />}
      </div>
    </>
  );
}

export default OrderList;
