import { Col, Modal, Row, Spin, Table, Tooltip, Timeline } from 'antd';
import orderApi from '../../../../apis/orderApi';
import helpers from '../../../../helpers';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { CarOutlined, ContainerOutlined } from '@ant-design/icons';
function OrderDetail(props) {
  const { orderId, onClose } = props;
  const [visible, setVisible] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [order, setOrder] = useState(null);

  // event: lấy chi tiết đơn hàng
  useEffect(() => {
    let isSubscribe = true;
    async function getOrderDetails() {
      try {
        const response = await orderApi.getOrderDetails(orderId);
        if (isSubscribe && response) {
          setOrder(response.data);
          setIsLoading(false);
        }
      } catch (error) {
        if (isSubscribe) {
          setIsLoading(false);
          setOrder(null);
        }
      }
    }
    getOrderDetails();
    return () => {
      isSubscribe = false;
    };
  }, [orderId]);

  // cột cho bảng chi tiết sản phẩm
  const columns = [
    {
      title: 'Product',
      dataIndex: 'product',
      key: 'product',
      render: (product, record) => {
        return (
          <Link to={`/product/${product.bookId}`}>
            <Tooltip title={product.title}>
              {helpers.reduceProductName(product.title, 40)}
            </Tooltip>
          </Link>
        )
      },
    },
    {
      title: 'Price',
      dataIndex: 'product',
      key: 'price',
      render: (product, record) => helpers.formatProductPrice(product.price),
    },
    {
      title: 'Quantity',
      dataIndex: 'quantity',
      key: 'quantity',
    },
    {
      title: 'Discount',
      dataIndex: 'discount',
      key: 'prod',
      render: (v, record) => `10 fix lại %`,
    },
    {
      title: 'Total',
      dataIndex: 'product',
      key: 'totalMoney',
      render: (product, record) => {
        const { price } = product;
        return helpers.formatProductPrice(
          price * record.quantity
        );
      },
    },
  ];

  // rendering...
  return (
    <Modal
      width={1000}
      centered
      visible={visible}
      onCancel={() => {
        setVisible(false);
        onClose();
      }}
      maskClosable={false}
      footer={null}
      title={
        <p className="font-size-18px m-b-0">
          Order Details
          {/* {order && ( */}
          {order && (
            <>
              {/* <span style={{ color: '#4670FF' }}>{` #${order.orderCode}`}</span>
              <b>{` - ${helpers.convertOrderStatus(order.orderStatus)}`}</b> */}
              <span style={{ color: '#4670FF' }}>{` #${order.id}`}</span>
              {/* <b>{` - ${helpers.convertOrderStatus(order.status)}`}</b> */}
              <b>{` - ${order.status[0].toUpperCase() + order.status.slice(1)}`}</b>
            </>
          )}
        </p>
      }>
      <>
        {(isLoading || !order) ? (
          <div className="pos-relative" style={{ minHeight: 180 }}>
            <Spin
              className="trans-center"
              tip="Loading...Please wait"
              size="large"
            />
          </div>
        ) : (
          <Row gutter={[16, 16]}>
            {/* thời gian đặt hàng */}
            <Col span={24} className="t-right">
              <b className="font-size-14px">
                {`Order Date  ${helpers.formatOrderDate(
                  order.createdAt,
                  1,
                )}`}
              </b>
            </Col>

            {/* địa chỉ người nhận */}
            <Col span={24} sm={12}>
              {order && (
                <>
                  <h3 className="t-center m-b-12">Delivery Address</h3>
                  <div
                    className="p-tb-12 p-lr-16 bg-gray bor-rad-8"
                    style={{ minHeight: 150 }}>
                    <h3 className="m-b-8">
                      {/* <b>{order.deliveryAdd.name.toUpperCase()}</b> */}
                      <b>{order.ShippingAddress.reciver.toUpperCase()}</b>
                    </h3>
                    {/* <p className="m-b-8">{`Địa chỉ: ${order.deliveryAdd.address}`}</p> */}
                    <p className="m-b-8">{`Address: ${order.ShippingAddress.details}, ${order.ShippingAddress.ward.prefix} ${order.ShippingAddress.ward.name}, ${order.ShippingAddress.ward.district.prefix} ${order.ShippingAddress.ward.district.name}, ${order.ShippingAddress.ward.district.province.name}.`}</p>
                    <p className="m-b-8">
                      {/* Số điện thoại: {order.deliveryAdd.phone} */}
                      Phone no: {order.ShippingAddress.phone}
                    </p>
                  </div>
                </>
              )}
            </Col>

            {/* Hình thức thanh toán */}
            <Col span={24} sm={12}>
              {order && (
                <>
                  <h3 className="t-center m-b-12">Payment Method</h3>
                  <div
                    className="p-tb-12 p-lr-16 bg-gray bor-rad-8 d-flex justify-content-center align-i-center"
                    style={{ minHeight: 150 }}>
                    <b className="m-b-8 font-size-20px">
                      {/* {helpers.convertPaymentMethod(order.paymentMethod)} */}
                      {helpers.convertPaymentMethod(order.payment)}
                    </b>
                  </div>
                </>
              )}
            </Col>
            {order && (
              <>
                {/* Chi tiết sản phẩm đã mua */}
                <Col span={24}>
                  <Table
                    pagination={false}
                    columns={columns}
                    dataSource={order.order_detail}
                    rowKey={'id'}
                  />
                </Col>

                {/* Tổng cộng */}
                <Col span={24} className="t-right">
                  <div className="d-flex font-weight-500 justify-content-end">
                    <p style={{ color: '#bbb' }}>Merchandise Subtotal</p>
                    <span
                      className="m-l-32"
                      style={{ color: '#888', minWidth: 180 }}>
                      {helpers.formatProductPrice(helpers.calTotalOrderFee(order.order_detail))}
                    </span>
                  </div>
                  <div className="d-flex font-weight-500 justify-content-end">
                    <p style={{ color: '#bbb' }}>Shipping</p>
                    <span
                      className="m-l-32"
                      style={{ color: '#888', minWidth: 180 }}>
                      {/* {helpers.formatProductPrice(order.transportFee)} */}
                      {helpers.formatProductPrice(order.transportFee)}
                    </span>
                  </div>
                  <div className="d-flex font-weight-500 justify-content-end">
                    <p style={{ color: '#bbb' }}>Voucher</p>
                    <span
                      className="m-l-32"
                      style={{ color: '#888', minWidth: 180 }}>
                      100
                    </span>
                  </div>
                  <div className="d-flex font-weight-500 justify-content-end">
                    <p style={{ color: '#bbb' }}>Total</p>
                    <span
                      className="m-l-32 font-size-18px"
                      style={{ color: '#ff2000', minWidth: 180 }}>
                      {/* {helpers.formatProductPrice(helpers.calTotalOrderFee(order))} */}
                      {order.total} $
                    </span>
                  </div>
                </Col>
                <Col span={12}>
                  <Timeline mode="left">
                    <Timeline.Item label="2015-09-01"
                      dot={
                        <CarOutlined
                          style={{
                            fontSize: '16px',
                          }}
                        />
                      }
                      color="red"
                    >Create a services</Timeline.Item>
                    <Timeline.Item label="2015-09-01 09:12:11">Solve initial network problems</Timeline.Item>
                    <Timeline.Item>Technical testing</Timeline.Item>
                    <Timeline.Item label="2015-09-01 09:12:11"
                      dot={
                        <ContainerOutlined
                          style={{
                            fontSize: '16px',
                          }}
                        />
                      }
                      color="green"
                    >Network problems being solved</Timeline.Item>
                  </Timeline>
                </Col>
              </>
            )}
          </Row>
        )}
      </>
    </Modal>
  );
}

OrderDetail.propTypes = {
  orderId: PropTypes.number,
  onClose: PropTypes.func,
};

export default OrderDetail;
